import React from "react";
import "./index.css"
import {Link} from "gatsby";


// @ts-ignore
const BurgerMenu = ({languages, links}) => {

    var height = typeof window === "undefined" ? "100vh" : window.innerHeight + "px";
    return (
        <nav role="navigation">
            <div id="menuToggle">

                <input type="checkbox"/>

                <span/>
                <span/>
                <span/>

                <ul id="menu" style={{
                    'height': height,
                    'minHeight': height, 'maxHeight': height
                }}>
                    {links.map((link:any) => <Link key={link.link} to={link.link}>
                        <li>
                          <h2>{link.name}</h2>
                        </li>
                    </Link>)}

                    <button>Kontakt</button>


                    {languages.map((link: any) => <a key={link.link} href={link.link}>
                        <li>{link.name}</li>
                    </a>)}


                </ul>
            </div>
        </nav>
    );

};

export default BurgerMenu;
