import React, {FunctionComponent} from 'react';
import style from './style.module.scss';

export enum Mode {
  FULL = style.full as any, PART = style.part as any
}

const Container: FunctionComponent<{ mode?: Mode }> = ({children, mode}) => {
  return (
    <div className={style.containerWrapper}>
      <div className={`${style.container}`}>
        <div className={`${mode || Mode.FULL}`}>
          {children}
        </div>
      </div>
    </div>
  );
};

export default Container;
