import React, {FunctionComponent, useEffect, useState} from "react"
import './style.css'
import Container from "../container";
import Button, {ButtonMode, ButtonSize} from "../button";
import {Link} from "gatsby";
import {acceptAll, acceptedTracking} from "../../../plugins/tracking";


interface CookiebannerText {
  header: string,
  description: string,
  more: string,
  changeSettings: string,
  accept: string,
}

const defaultTexts: CookiebannerText = {
  header: "Cookies",
  description: "Wir verwenden Cookies, um die korrekte Funktionsweise dieser Website sicherzustellen, Ihnen den bestmöglichen Service zu bieten, die Benutzerfreundlichkeit zu verbessern und unsere Website zu optimieren.",
  more: "Erfahren Sie mehr darüber, wie wir Cookies verwenden",
  changeSettings: "Einstellungen ändern",
  accept: "Akzeptieren"
}

const CookieBanner: FunctionComponent<{ dataProtectionLink: string, cookieDetailLink: string, texts?: CookiebannerText }> =
  ({dataProtectionLink, cookieDetailLink, texts}) => {

    texts = {...defaultTexts, ...texts};
    var isGatsbyRender = typeof window === "undefined";
    const [, forceUpdate] = React.useReducer(x => x + 1, 0);
    const [visible, setVisible] = useState(isGatsbyRender);

    useEffect(() => setVisible(!acceptedTracking()), [])

    return <div className={`cookie asd ${visible ? 'hiddencookie' : ''}`}>
      <Container>
        <div className={"innercookie"}>
          <img
            alt={"cookie-icon"}
            src={"data:image/png;base64,"}/>
          <div className={"cookietext"}>
            <div>
              <h3 className={"cookieheader"}>{texts.header}</h3>
              <p className={"cookiedescription"}>{texts.description}</p>
              <div className={"cookielinks"}>
                <b><Link to={dataProtectionLink}>{texts.more}</Link></b>
                |
                <b><Link to={cookieDetailLink}/></b>
              </div>
            </div>
            <div className={"cookieOpts"}>
              <Button buttonSize={ButtonSize.BIG} buttonMode={ButtonMode.OUTLINE} onClick={() => {
                acceptAll();
                forceUpdate();
              }}>
                {texts.accept}
              </Button>
              <b><Link to={cookieDetailLink}>Einstellungen ändern</Link></b>
            </div>
          </div>
        </div>
      </Container>
    </div>
  }
export default CookieBanner

